@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
.deletePostContentClass
{
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    border: 2px solid orange;
    border-radius: 10px;
    padding: 20px;
    background-color: white;
}
.modalOverlayClass
{
    position: fixed;
    z-index: 2;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.8);
}
.jobs
{
    padding-top: 90px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    row-gap: 20px;
    font-family: "Lobster", sans-serif;
    font-weight: 400;
    font-style: normal;
}
.empty
{
    position: relative;
    top: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 90px);
    font-size: 30px;
}
.jobs > div
{
    background: linear-gradient(135deg, rgb(255 213 0 / 10%), rgb(251 248 248 / 30%));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    height: min-content;
}
.jobs > div > div
{
    display: flex;
    align-items: center;
    justify-content: center;
}
.jobs span
{
    font-weight: 500;
}
.title
{
    color: white;
    font-size: 35px;
    padding: 10px;
}
.description
{
    font-size: 20px;
    padding: 7px;
    color: aquamarine;
}
.user
{
    font-size: 24px;
    padding: 4px;
    color: pink;
}
.container
{
    display: flex;
    justify-content: space-between !important;
    font-size: 18px;
    padding: 5px 0px;
    color: yellow;
    width: 100%;
}
.location
{
    font-size: 24px;
    padding: 4px;
    color: wheat;
}
.delete
{
    position: absolute;
    color: orangered;
    top: 20px;
    right: 20px;
    font-size: 1.7rem;
    cursor: pointer;
}
.confirmation
{
    font-size: 24px;
    padding: 20px 0px 40px 0px;
}
.buttonContainer
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.yes
{
    background-color: #fe4141;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
}
.no
{
    background-color: #05c405;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
}
.loader
{
    position: relative;
    top: 90px;
    height: calc(100vh - 90px);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media only screen and (width <= 503px)
{
    .deletePostContentClass{width: 87%;}
}
@media only screen and (width <= 489px)
{
    .jobs > div
    {
        width: 300px;
    }
    .empty{font-size: 6.7cqi;}
}
@media only screen and (width <= 366px)
{
    .jobs > div
    {
        width: 80cqi;
    }
    .title{font-size: 9cqi;}
    .description{font-size: 5.14cqi;}
    .user{font-size: 6.17cqi;}
    .container{font-size: 4.63cqi;}
    .location{font-size: 6.17cqi;}
    .apply{font-size: 7.71cqi;}
    .deletePostContentClass
    {
        width: 83%;
        padding: 5px;
        padding-bottom: 20px;
    }
    .confirmation{font-size: 16px;}
    .yes , .no
    {
        font-size: 14px;
        width: 70px;
        height: 30px;
    }
}